<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>显示系统所使用的消费者保障服务信息</li>
      <li>设置为启用可供商家使用</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>服务名称</label>
        <el-input v-model="queryParams.serviceName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button icon="add" type="success" size="small" @click="$routerLink('/consumerServices/save','save')">新增</cms-button>
      <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="serviceName" label="服务名称"  min-width="160">
        <template slot-scope="scope">
          <div style="display:flex;">
            <div class="table-img" style="width:auto;border:0">
             <img v-if="scope.row.resourcesSpaceData" style="width:20px;height:20px;"
              :src="$cms.filterUrl(scope.row.resourcesSpaceData.url) " alt="logo" />
           </div>
           <p style="max-width:310px" class="service-overflow" :title="scope.row.serviceName">{{scope.row.serviceName}}</p>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="bond" label="保证金额度" min-width="90">
        <template slot-scope="scope">
          <span>{{$cms.float(scope.row.bond)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="isEnable" label="是否启用" width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.isEnable" @change="switchShow(scope.row.id,scope.row.isEnable)"></cms-switch>
        </div>
      </el-table-column>
      <el-table-column label="排序" prop="sortNum">
        <template slot-scope="scope">
          <el-input v-model="scope.row.sortNum" @keyup.enter.native="sortNum($event,scope.row.id)" maxlength="2"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button  icon='edit' size='small' type='info' @click.stop="$routerLink('/consumerServices/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button  icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  consumerServicesList,
  consumerServicesDel,
  consumerServicesSort,
  consumerServicesEnable
} from '@/api/setting/consumerServices.js'
var defaultData = {
  serviceName: ''
}
export default {
  name: 'consumerServicesList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 删除
      this.deleteTableData(consumerServicesDel, id)
    },
    deleteDatas() {
      // 删除
      this.deleteTableDatas(consumerServicesDel, this.ids)
    },
    switchShow(id, status) { // 是否启用
      consumerServicesEnable({ id: id, status: status }).then(res => {
        if (res.code === 200) {
          if (status) {
            this.$successMessage('启用成功')
          } else {
            this.$successMessage('禁用成功')
          }
        } else {
          this.$errorMessage(res.code)
        }
      })
    },
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          consumerServicesSort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(consumerServicesList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(consumerServicesList, this.queryParams)
  }
}
</script>

<style scoped>
.service-overflow{
  overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    margin-left: 10px;
    align-self: center;
}
</style>



